import React, { useEffect, useState, useRef, useCallback } from 'react';
import styled from "styled-components";
import { UserData } from "../../model/UserData";
import { UserApi } from "../../api/app/UserApi";
import UserListTableHeader from "../../component/user-list/UserListTableHeader";
import UserListTableItem from "../../component/user-list/UserListTableItem";
import PagingTypeSelector from "../../component/user-list/PagingTypeSelector";
import CacheStatusBadge from '../../component/user-list/CacheStatusBadge';
import Lottie from "lottie-react";
import loadingAnimation from "../../resource/icon/loading.json";
import * as Swal from '../../api/common/alert';
import { PAGE_SIZE } from '../../constants/pageSize';

const Container = styled.div`
  display: flex;
  flex-direction: column;
  margin: 0px 149px 20px;
`;

const LoadingWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
`;

const UserListPage = () => {
  const [userList, setUserList] = useState<UserData[]>([]); // userList:    
  const [totalCount, setTotalCount] = useState(0); // totalCount:    
  const [cursor, setCursor] = useState<number | null>(null); // cursor:   id
  const [hasMore, setHasMore] = useState(true); //    
  const [isLoading, setIsLoading] = useState(false); //   
  const observerTarget = useRef<HTMLDivElement>(null); //     
  const [cacheStatus, setCacheStatus] = useState<{ type: 'HIT' | 'MISS', message: string, speed: string } | null>(null); //   (HIT/MISS/ )
  const [ttlSeconds, setTtlSeconds] = useState<number | null>(null); //   TTL() 
  const [cacheVisible, setCacheVisible] = useState(false); //     
  const cacheTimeoutRef = useRef<number | null>(null); //     
  const [pagingType, setPagingType] = useState<'cache-cursor' | 'cursor' | 'offset'>(  // |:  :        
    () => (localStorage.getItem('pagingType') as 'cache-cursor' | 'cursor' | 'offset') || 'cache-cursor'
  );
  const [page, setPage] = useState(0); // offset    
  const refreshLottieRef = useRef<any>(null);

  // TTL 
  useEffect(() => {
    if (ttlSeconds === null || ttlSeconds <= 0) return; // TTL   0     

    const interval = setInterval(() => { // setInterval(code, 1000):    1 
        setTtlSeconds(prev => {
          if (prev !== null) {
            if (prev <= 1) { // ttl  1 
              clearInterval(interval); //  
              setCacheStatus({ type: 'MISS', message: ' ', speed: '-' }); //   
              return 0;
            }
            return prev - 1; // ttl  1   -1  
          }
          return null;
        });
    }, 1000);

    return () => clearInterval(interval); //  
  }, [ttlSeconds]);

  const loadUsers = useCallback(async (customCursor = cursor, customPage = page) => {
    if (!hasMore || isLoading) return; //         

    try {
      setIsLoading(true); // isLoading true  =>  (  )
      const response = await UserApi.getUser(customCursor, customPage, PAGE_SIZE, pagingType); //   API
      //   
      const data = response.data; //  
      //    
      const ttlHeader = response.headers['x-ttl']; //  TTL 
      const cacheHeader = response.headers['x-cache']; //  HIT/MISS 
      const responseTime = response.headers['x-response-time']; //  

      //      
      setCacheVisible(false);

      // 0.2   
      setTimeout(() => {
        //    
        if (cacheHeader === 'HIT') {
            const ttl = parseInt(ttlHeader, 10); // parseInt('300', 10):  10  
            setTtlSeconds(ttl); // TTL   
            //   
            setCacheStatus({
                type: 'HIT',
                message: `  HIT `,
                speed: responseTime
            });
        } else {
            setCacheStatus({
                type: 'MISS',
                message: `  MISS `,
                speed: responseTime
            });
        }

        //    
        setCacheVisible(true);

        //    
        if (cacheTimeoutRef.current) {
            clearTimeout(cacheTimeoutRef.current);
        }

        //   
        cacheTimeoutRef.current = window.setTimeout(() => {
            setCacheVisible(false); //    
            cacheTimeoutRef.current = null;
        }, 4000);
      }, 200);

      setUserList(prev => [...prev, ...data]); // prev( userList) data(  )  

      if(data.length > 0) {
          //    id cursor  
          setCursor(data[data.length -1].id); // data[0]       -1 
          setHasMore(true);
          setPage(prev => prev + 1); //  1 
      } else {
          setHasMore(false);
      }

    } catch (error) {
      console.error('Failed to load users:', error);
      Swal.alert(error, '', 'error');
    } finally {
      setIsLoading(false); // isLoading false  =>   X
    }
  }, [cursor, hasMore, isLoading, pagingType]);

  useEffect(() => {
    // observer(IntersectionObserver ) 
    const observer = new IntersectionObserver( // IntersectionObserver:  (observerTarget) ()   API
      entries => { // entries: IntersectionObserver     
        if (entries[0].isIntersecting) { // entries[0]:     , isIntersecting:     true,  false
          loadUsers();
        }
      },
      { threshold: 0.1, rootMargin: '100px' } // threshold: 0.1: 10%     , rootMargin: '100px': () 100px       loadUsers() 
    );

    if (observerTarget.current) {
      // observer 
      observer.observe(observerTarget.current); // observe():       
    }

    // return():   - "useEffect  "  "  " 
    return () => observer.disconnect(); // disconnect():  
  }, [loadUsers]);

  //    
  const fetchTotalCount = useCallback(async () => {
    try {
        const count = await UserApi.getUserCount();
        setTotalCount(count);
    } catch (error) {
        console.error('Failed to load total count:', error);
        Swal.alert(error, '', 'error');
    }
  }, []);

  useEffect(() => {
    fetchTotalCount();
  }, [fetchTotalCount]);

  //  
  const handleUserUpdate = (userId: number, newName: string) => {
    setUserList(prev =>
      prev.map(user =>
        user.id === userId ? { ...user, name: newName } : user // id   user  (...user)  name newName (name: newName)
      )
    );
  };

  //  
  const handleUserDelete = (userName: string) => {
    setUserList(prev => prev.filter(user => user.name !== userName)); // filter()  user.name userName    
    setTotalCount(prev => Math.max(0, prev - 1)); //     -1 
  };

  //    
  const handlePagingTypeChange = (type: 'cache-cursor' | 'cursor' | 'offset') => {
    setPagingType(type);
    localStorage.setItem('pagingType', type); //     
  };

  const handleReset = async () => {

    // Lottie  
    if (refreshLottieRef.current) {
        refreshLottieRef.current.stop();
        refreshLottieRef.current.play();
    }

    //  
    setUserList([]);
    setHasMore(true);
    setCursor(null);
    setPage(0);

    // state        
    await loadUsers(null, 0);
    await fetchTotalCount();   //     

  };

  return (
    <Container>
      <PagingTypeSelector currentType={pagingType} onChange={handlePagingTypeChange} />

      <UserListTableHeader userCount={totalCount} searchedCount={userList.length} onReset={handleReset} ref={refreshLottieRef} />
      {userList.map(item => (
        <UserListTableItem
          key={item.id}
          data={item}
          onUpdate={handleUserUpdate}
          onDelete={handleUserDelete}
          pageSize={PAGE_SIZE}
        />
      ))}

      {/*    */}
      <CacheStatusBadge visible={cacheVisible} type={cacheStatus?.type || 'MISS'} message={cacheStatus?.message} speed={cacheStatus?.speed} ttlSeconds={ttlSeconds} />

      {/*    Lottie   */}
      {isLoading && (
        <LoadingWrapper>
          <Lottie animationData={loadingAnimation} loop={true} style={{ width: 200, height: 200 }} />
        </LoadingWrapper>
      )}

      <div ref={observerTarget} style={{ height: '100px' }} />
    </Container>
  );
};

export default UserListPage;