import React, { forwardRef, useRef, useImperativeHandle } from 'react';
import styled from "styled-components";
import {Colors} from "../../resource/Colors";
import Lottie, { LottieRefCurrentProps } from "lottie-react";
import refreshAnimation from "../../resource/icon/refresh.json";

const Container = styled.header`
  position: sticky;
  background-color: #fff;
  top: 108px;
  z-index: 99;
  width: 100%;
  height: 62px;
  display: flex;
  box-sizing: border-box;
  flex-direction: row;
  align-items: center;
  border-bottom: 1px solid black;
  padding: 0px 0px 0px 20px;
`;

const Title = styled.p`
  position: absolute;
  margin: 0;
  font-size: 20px;
  font-weight: 800;
  color: ${Colors.primaryColor};
`;

const TitleRightContainer = styled.div`
  margin-left: auto;
  display: flex;
  align-items: center;
  font-size: 20px;
  font-weight: 700;
  color: ${Colors.primaryColor};
`;

const Highlighted = styled.span`
  color: darkorange; //  
`;

const Separator = styled.span`
  margin: 0 10px;
  color: #6c757d; //   
  font-weight: 400;
`;

const ResetButton = styled.button`
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  background: none;
  cursor: pointer;
`;

interface UserListTableHeaderProps {
  userCount: number;
  searchedCount: number;
  onReset: () => void; //   
}
// forwardRef:     useRef   
// forwardRef<ref , props >(props, ref)
const UserListTableHeader = forwardRef<LottieRefCurrentProps, UserListTableHeaderProps>(({ userCount, searchedCount, onReset }, ref) => {
    const formattedTotalCount = userCount.toLocaleString();
    const formattedSearchedCount = searchedCount.toLocaleString();

    // LottieRefCurrentProps: Lottie    
    const lottieRef = useRef<LottieRefCurrentProps>(null);

    // useImperativeHandle(  ref,   ): ref    lottieRef.current 
    // ,      DOM    lottieRef.current 
    useImperativeHandle(ref, () => lottieRef.current as LottieRefCurrentProps); // lottieRef.current: Lottie  

    return (
        <Container>
            <Title> </Title>
            <Title style={{ left: '22%' }}></Title>
             <TitleRightContainer>
                <Highlighted>{formattedSearchedCount}  </Highlighted>
                <Separator>|</Separator>
                <span>   : {formattedTotalCount} </span>
                <ResetButton onClick={onReset}>
                    <Lottie lottieRef={lottieRef} animationData={refreshAnimation} loop={false} style={{ width: 45, height: 45 }} />
                </ResetButton>
              </TitleRightContainer>
        </Container>
    );
});

export default UserListTableHeader;