import axios from "axios";
import * as Swal from '../../api/common/alert';

// RequestMethod: HTTP   (enum) 
// ex) RequestMethod.GET => 'get'
export enum RequestMethod {
  GET = 'get',
  POST = 'post',
  PUT = 'put',
  DELETE = 'delete'
}

// request  
export const request = async <T>( // await    async . async   await  
  method: RequestMethod,
  uri: string,
  params: any,
  data: any,
  responseType: 'data' | 'full' = 'data' // 'data' or 'full'       but      ,  'data' 
): Promise<any> => { // Promise:     
  try {
    // JWT   
    const token = document.cookie // document.cookie:       
      .split('; ') // split():      
      .find(row => row.startsWith('accessToken=')) // find():        
      ?.split('=')[1]; // ex) 'accessToken=abcdef12345' 'abcdef12345'  

    // axios API 
    const response = await axios(uri, {
      method,
      params,
      data,
      baseURL: '/',
      headers: {  // headers: HTTP  
        ...(token && { Authorization: `Bearer ${token}` }) // token   Authorization  
      }
    })

    // full response,  response.data 
    return responseType === 'full' ? response : response.data;

  } catch (error: any) {
    console.log("error : ", error);

    if (error.code && error.code === 'ERR_NETWORK') {
      Swal.alert('  ,  .', '', 'error')
    }

    if (error.response.status === 404) {
      Swal.alert(` URI    . : ${method} /${uri}`, '', 'error')
    }

    if (error.response.status === 500) {
      Swal.alert('  .', '', 'error')
    }

    throw error.response || { message: '    .' };

  }
}